/*----------------------------------------------------------------------------
  WordPress Social Menu
  - make sure to include FontAwesome for this to work!
----------------------------------------------------------------------------*/
.menu-social {
  ul {
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    text-align: inherit;
    list-style: none;
    li{
      display: inline-block;
      position: relative;
      padding: 0 .625rem;
      width: 50%;
      a{
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: flex-start;
        color: $primary;
        font: 400 rem(18)/1.19 $mont;
        &:before{
          display: inline-block;
          font-style: normal;
          font-variant: normal;
          text-rendering: auto;
          -webkit-font-smoothing: antialiased;
          font-family: 'Font Awesome 5 Brands';
          font-weight: 400;
          font-size: rem(24);
          color: $primary;
          transition: all .4s ease;
          margin-right: rem(10);
        }
        &:hover {
          color: $gray-light;
          &:before {
              color: $gray-light;
            }
        }
      } // end anchor
      // Adding the icons
      a[href*="facebook.com"]::before {
        content: '\f09a';
      }

      a[href*="twitter.com"]::before {
        content: '\f099';
      }

      a[href*="dribbble.com"]::before {
        content: '\f17d';
      }

      a[href*="plus.google.com"]::before {
        content: '\f0d5';
      }

      a[href*="pinterest.com"]::before {
        content: '\f0d2';
      }

      a[href*="github.com"]::before {
        content: '\f09b';
      }

      a[href*="tumblr.com"]::before {
        content: '\f173';
      }

      a[href*="youtube.com"]::before {
        content: '\f167';
      }

      a[href*="flickr.com"]::before {
        content: '\f16e';
      }

      a[href*="vimeo.com"]::before {
        content: '\f194';
      }

      a[href*="instagram.com"]::before {
        content: '\f16d';
      }

      a[href*="linkedin.com"]::before {
        content: '\f0e1';
      }

      a[href*="yelp.com"]::before {
        content: '\f1e9';
      }

      a[href*="mailto:"]::before {
        content: '\f0e0';
        font-family: 'Font Awesome 5 Pro';
        font-weight: 900;
      }
    } // end li
  } // end ul
} // end .menu-social

// Hide the default Screen Reader text
.screen-reader-text {
  display: none;
}
